import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "*",
      redirect: "/path_planning"
    },
    {
      path: "/path_planning",
      name: "PathPlanning",
      component: () => import("@/views/PathPlanning")
    },
    {
      path: "/monitor",
      name: "Monitor",
      component: () => import("@/views/Monitor")
    }
  ]
});



