<template>
  <div>
    <div class="menu">
      <div class="menu_title"></div>
      <div class="menu_split"></div>
      <div class="menu_item">
        <router-link to="/path_planning">规划数据集</router-link>
      </div>
      <div class="menu_item">
        <router-link to="/monitor">采集数据集</router-link>
      </div>
    </div>
    <div></div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {},
  methods: {
    jumpUrl(src) {
      window.location.href = src;
    }
  }
};
</script>

<style>
.menu {
  display: flex;
  background: #253e5c;
  height: 63px;
}
.menu_title {
  height: 50px;
  width: 160px;
  overflow: hidden;
  background: url(./icon.png) 50% 50% / 140px 32px no-repeat;
}
.menu_split {
  flex-grow: 1;
}
.menu_item > a {
  font-size: 14px;
  font-weight: 600;
  color: #fff;
  line-height: 50px;
  margin: 0 7px;
  text-decoration: none;
}

*::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
*::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 15px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  /*background: #cce2f7;*/
  /* background: #999; */
}
*::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background: #ededed;
}

*:-webkit-autofill {
  box-shadow: 0 0 0px 1000px #323451 inset !important;
  -webkit-box-shadow: 0 0 0px 1000px #323451 inset !important;
  border: 0px solid #ccc !important;
  -webkit-text-fill-color: #fff;
}
</style>



